  <script>
  //   
  function throttle(fn, wait = 200) {
    let last = 0;
    return function (...args) {
      const now = Date.now();
      if (now - last > wait) {
        last = now;
        fn.apply(this, args);
      }
    };
  }
  
  /* =====  &  ===== */
  const BEST_BADGE_UID = '246490729';                 //  UID
